/* Product Modal Overlay */
.product-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  padding: 20px;
  animation: fadeIn 0.3s ease;
  overflow-y: auto;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.product-modal-content {
  background: white;
  border-radius: 16px;
  max-width: 1000px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  animation: slideUp 0.3s ease;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

@keyframes slideUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modal-close {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: white;
  border: none;
  font-size: 20px;
  color: #666;
  cursor: pointer;
  z-index: 10;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-close:hover {
  background: #f5f5f5;
  color: #333;
  transform: rotate(90deg);
}

.modal-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0;
}

/* Left Side - Image */
.modal-image-section {
  background: #f8f9fa;
  padding: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 16px 0 0 16px;
}

.modal-image-section img {
  width: 100%;
  max-width: 400px;
  object-fit: contain;
}

.pre-order-badge,
.sale-badge {
  position: absolute;
  top: 20px;
  left: 20px;
  padding: 8px 16px;
  border-radius: 6px;
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;
  z-index: 5;
}

.pre-order-badge {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
}

.sale-badge {
  background: #dc3545;
  color: white;
}

/* Right Side - Details */
.modal-details-section {
  padding: 40px;
  overflow-y: auto;
  max-height: 90vh;
}

.modal-product-info {
  margin-bottom: 30px;
  padding-bottom: 30px;
  border-bottom: 2px solid #f0f0f0;
}

.product-group {
  color: #ff4d8d;
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 8px;
}

.modal-details-section h2 {
  font-size: 28px;
  font-weight: 700;
  color: #1a1f36;
  margin-bottom: 16px;
  line-height: 1.3;
}

/* Average Rating Display */
.average-rating {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 16px;
}

.average-rating .stars {
  display: flex;
  gap: 4px;
}

.average-rating .stars i {
  font-size: 16px;
  color: #ddd;
}

.average-rating .stars i.filled {
  color: #ffc107;
}

.average-rating .rating-text {
  font-size: 14px;
  color: #64748b;
  font-weight: 600;
}

.price-section {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
}

.current-price {
  font-size: 32px;
  font-weight: 700;
  color: #ff4d8d;
}

.original-price {
  font-size: 20px;
  color: #999;
  text-decoration: line-through;
}

.stock-info {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: #64748b;
  margin-bottom: 24px;
  font-weight: 600;
}

.stock-info i {
  color: #ff4d8d;
}

.modal-actions {
  display: flex;
  gap: 12px;
  margin-bottom: 16px;
}

.modal-actions .btn {
  flex: 1;
  padding: 14px 24px;
  border-radius: 8px;
  font-weight: 700;
  font-size: 15px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.btn-add-cart {
  background: linear-gradient(135deg, #ff4d8d, #e63e7d);
  color: white;
}

.btn-add-cart:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 77, 141, 0.3);
}

.btn-add-cart:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.btn-preorder {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
}

.btn-preorder:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.3);
}

.btn-wishlist-outline {
  background: white;
  border: 2px solid #ff4d8d;
  color: #ff4d8d;
  flex: 0 0 auto;
  width: 50px;
}

.btn-wishlist-outline:hover {
  background: #ff4d8d;
  color: white;
}

.preorder-slots {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  background: #f8f9fa;
  border-radius: 8px;
  font-size: 14px;
  color: #64748b;
  font-weight: 600;
}

.preorder-slots i {
  color: #667eea;
}

.preorder-slots .sold-out {
  color: #dc3545;
}

/* Review Section */
.review-section {
  margin-top: 30px;
  padding-top: 30px;
  border-top: 2px solid #f0f0f0;
}

.review-section h3 {
  font-size: 20px;
  font-weight: 700;
  color: #1a1f36;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.review-section h3 i {
  color: #ffc107;
}

.login-to-review {
  text-align: center;
  padding: 40px 20px;
  background: #f8f9fa;
  border-radius: 8px;
  color: #64748b;
}

.login-to-review i {
  font-size: 40px;
  margin-bottom: 12px;
  color: #ccc;
}

.login-to-review p {
  margin: 0;
  font-size: 15px;
  font-weight: 600;
}

/* Rating Input */
.rating-input {
  margin-bottom: 20px;
}

.rating-input label {
  display: block;
  font-weight: 600;
  color: #1a1f36;
  margin-bottom: 10px;
  font-size: 14px;
}

.stars-input {
  display: flex;
  gap: 8px;
}

.stars-input i {
  font-size: 32px;
  color: #ddd;
  cursor: pointer;
  transition: all 0.2s ease;
}

.stars-input i:hover,
.stars-input i.active {
  color: #ffc107;
  transform: scale(1.1);
}

/* Review Input */
.review-input {
  margin-bottom: 20px;
}

.review-input label {
  display: block;
  font-weight: 600;
  color: #1a1f36;
  margin-bottom: 10px;
  font-size: 14px;
}

.review-input textarea {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  font-size: 14px;
  font-family: inherit;
  resize: vertical;
  transition: all 0.3s ease;
}

.review-input textarea:focus {
  outline: none;
  border-color: #ff4d8d;
  box-shadow: 0 0 0 3px rgba(255, 77, 141, 0.1);
}

.btn-submit-review {
  width: 100%;
  padding: 12px 24px;
  background: linear-gradient(135deg, #28a745, #218838);
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 700;
  font-size: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.btn-submit-review:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(40, 167, 69, 0.3);
}

.btn-submit-review:disabled {
  background: #ccc;
  cursor: not-allowed;
}

/* All Reviews */
.all-reviews {
  margin-top: 30px;
  padding-top: 30px;
  border-top: 2px solid #f0f0f0;
}

.all-reviews h4 {
  font-size: 16px;
  font-weight: 700;
  color: #1a1f36;
  margin-bottom: 20px;
}

.review-item {
  padding: 16px;
  background: #f8f9fa;
  border-radius: 8px;
  margin-bottom: 12px;
}

.review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.reviewer-info {
  display: flex;
  align-items: center;
  gap: 8px;
}

.reviewer-info i {
  font-size: 20px;
  color: #64748b;
}

.reviewer-info strong {
  font-size: 14px;
  color: #1a1f36;
}

.review-stars {
  display: flex;
  gap: 3px;
}

.review-stars i {
  font-size: 14px;
  color: #ddd;
}

.review-stars i.filled {
  color: #ffc107;
}

.review-text {
  font-size: 14px;
  color: #1a1f36;
  line-height: 1.6;
  margin: 0 0 8px 0;
}

.review-date {
  font-size: 12px;
  color: #94a3b8;
}

/* Responsive */
@media (max-width: 768px) {
  .modal-grid {
    grid-template-columns: 1fr;
  }

  .modal-image-section {
    border-radius: 16px 16px 0 0;
    padding: 30px;
  }

  .modal-details-section {
    padding: 30px 24px;
  }

  .modal-details-section h2 {
    font-size: 24px;
  }

  .current-price {
    font-size: 28px;
  }

  .modal-actions {
    flex-direction: column;
  }

  .btn-wishlist-outline {
    width: 100%;
  }

  .stars-input i {
    font-size: 28px;
  }
}

@media (max-width: 576px) {
  .product-modal-content {
    margin: 0;
    border-radius: 12px;
  }

  .modal-image-section {
    padding: 24px;
  }

  .modal-details-section {
    padding: 24px 20px;
  }

  .modal-close {
    width: 36px;
    height: 36px;
    font-size: 18px;
  }

  .current-price {
    font-size: 24px;
  }

  .stars-input i {
    font-size: 24px;
    gap: 6px;
  }
}